import { Link, useStaticQuery, graphql } from 'gatsby';
import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import LogoImage from '../LogoImage';

const Header = () => {
  const data = useStaticQuery(graphql`
  query HeaderQuery {
    site {
      siteMetadata {
        company
      }
    }
  }
  `);

  return (
    <header className="sticky top-0 bg-white shadow">
      <div className="container flex flex-col sm:flex-row justify-between items-center mx-auto py-4 px-8">
        <Link to="/" className="flex items-center text-2xl">
          <div className="w-12 mr-3">
            <LogoImage/>
          </div>
          <span className="font-medium text-2xl tracking-tight">{data.site.siteMetadata.company}</span>
        </Link>
        <div className="flex mt-4 sm:mt-0">
          <AnchorLink className="px-4" href="#features">Features</AnchorLink>
          <AnchorLink className="px-4" href="#testimonials">Testimonials</AnchorLink>
        </div>
      </div>
    </header>
  );
};

export default Header;
